.banners {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    width: 100%;
    gap: 12px;
    box-sizing:border-box;
    max-height: 1050px;
    overflow-y: auto;
}

@media (min-width: 768px) {
    .banners {
        max-height: 1050px;
        overflow-y: auto;
    }
}

